@import url('https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,600;0,700;1,600;1,700&display=swap');

.rules{
	position:fixed;
	top:0;
	bottom: 0;
	right: 0;
	left: 0;
	display: none; 
	background-color: rgba(14, 14, 22, 0.5);
}
.rules_content{
	padding: 25px;
	margin: auto;
	color: hsl(229, 62%, 25%);;
	background-color: white;
	border-radius: 16px;
	min-width: 250px;
}
.title{
	text-transform: uppercase;
	font-family: 'Barlow Semi Condensed', sans-serif;
	display: flex;
	font-size: 26px;
	font-weight: bold;
	justify-content: space-between;
	margin-bottom: 20px;
}
.button{
	padding: 3px 10px;
	border: none;
	font: inherit;
	color: inherit;
	background-color: transparent;
	cursor: pointer;

}

.rules.active{
	display: flex;
	justify-content: center;
	align-items: center;
}