$PaperGradient: linear-gradient(55deg, hsl(230, 93%, 52%), hsl(230, 84%, 68%));


.paper{
	position: absolute;
	width: 150px;
	height: 150px;
	border-radius: 50%;
	border: none;
	padding: 3px 10px;	
	color: inherit;
	background: $PaperGradient;
	cursor: pointer;
	box-shadow: 0px -8px 0px 0px rgba(black, 0.2) inset;
}
.innerElem{
	box-shadow: 0px 8px 0px 0px rgba(black, 0.2) inset;
	width: 80%;
	height: 73%;
	border-radius: 50%;
	margin: auto;
	background-color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
}
.icon{
	margin: auto;
}

@media (max-width:767px) {
	.paper{
		margin: 10% 0 0 20%;
		position: absolute;
		width: 120px;
		height: 120px;
	}
	.paper.active{
		margin: 0px;
	}
}