$ScissorsGradient: linear-gradient(45deg, hsl(39, 89%, 49%), hsl(40, 84%, 53%));


.scissors{
	position: absolute;
	width: 150px;
	height: 150px;
	border-radius: 50%;
	border: none;
	padding: 3px 10px;
	color: inherit;
	background: $ScissorsGradient;
	box-shadow: 0px -8px 0px 0px rgba(black, 0.2) inset;
	cursor: pointer;
}
.innerElem{
	box-shadow: 0px 8px 0px 0px rgba(black, 0.2) inset;
	width: 80%;
	height: 73%;
	border-radius: 50%;
	margin: auto;
	background-color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
}
.icon{
	margin: auto;
}

@media (max-width:767px) {
	.scissors{
		margin: 10% 20% 0 0;
		position: absolute;
		width: 120px;
		height: 120px;
	}
}