$SpockGradient: linear-gradient(45deg,hsl(189, 59%, 53%), hsl(189, 58%, 57%));


.spock{
	position: absolute;
	width: 150px;
	height: 150px;
	border-radius: 50%;
	border: none;
	padding: 3px 10px;
	color: inherit;
	background: $SpockGradient;
	cursor: pointer;
	box-shadow: 0px -8px 0px 0px rgba(black, 0.2) inset;

}
.innerElem{
	box-shadow: 0px 8px 0px 0px rgba(black, 0.2) inset;
	width: 80%;
	height: 73%;
	border-radius: 50%;
	margin: auto;
	background-color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
}
.icon{
	margin: auto;
}

@media (max-width:767px) {
	.spock{
		
		position: absolute;
		width: 120px;
		height: 120px;
	}
}