$width: 40%;
$height: 50%;
.game{
	position: relative;
	margin: 30px auto;
	width: 80%;
	height: 330px;
	background-image: url(./bg-triangle.svg);
	background-position: 50%; 
	background-size: $width $height;
	background-repeat: no-repeat;
	display: grid;
	grid-template-rows: 1fr 80px;
}	

.board{
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr 1fr;
	grid-template-areas: 'paper scissors'
						 'rock rock';
	row-gap: 15%;
	height: 90%;
	width: 70%;
	margin: auto;
}
.paper{
	display: block;
	justify-content: center;
	align-content: center;

	grid-area: paper;
	
}
.rock{
	display: block;
	grid-area: rock;
	align-self: center;
	justify-content: center;
	align-content: center;
	
}
.scissors{
	display: block;
	grid-area: scissors;
	justify-content: center;
	align-content: center;
	

}
.board2{
	display: none;
}
.spock{
	display: none;
}
.lizard{
	display: none;
}
.game.active{
	margin: 20px auto;
	box-sizing: content-box;
	background-image: url(./bg-pentagon.svg);
	width: 500px;
	height: 430px;
	.board{
		display: none;
	}
	.board2{
		padding: 0px 30px;
		display: block;
		box-sizing: content-box;
		row-gap: 0px;
		height: 330px;
	}
	.row1{
		margin-top: 30px;
		height: 100px;
		width: 100%;
		display: flex;
		align-content: center;
		justify-content: center;
	}
	.row2{
		margin: 0 10px;
		padding: 0 20px;
		height: 130px;
		width: auto;
		display: flex;
		align-content: center;
		justify-content: space-between;
	}
	.row3{
		margin: 0 10px;
		padding: 0 20px;
		height: 140px;
		width: auto;
		display: flex;
		align-content: center;
		align-items: center;
		justify-content: space-around;
	}

	.paper{
		grid-row:2/3;
		grid-column:5/6;
		
	}
	.rock{
		grid-row:3/4;
		grid-column:4/5;
		
	}
	.scissors{

		grid-row: 1/2;
		grid-column: 3/4;
	
	}
	.spock{
		display: block;
		grid-row:2/3;
		grid-column:1/2;
	}
	.lizard{
		display: block;
		grid-row:3/4;
		grid-column:2/3;
	}
}

@media (max-width:767px) {
	.game{
		width: 100%;
		height: 50%;
		.board{
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-template-rows: 1fr 1fr;
			grid-template-areas: 'paper scissors'
								 'rock rock';
			row-gap: 15%;
			height: 90%;
			width: 100%;
			margin: auto;
		}
	}
	.game.active{
		width: 100%;
		height: 50%;
		.row1{

		}
		.row2{
			padding: 0px 30px;
			margin: 0px 10%;
		}
		.row3{
			padding: 0px 30px;
			margin: 0px 70px;
		}
	}

}

@media (max-width:600px) {
	.game.active{
		background-size: 60% auto;
		width: 100%;
		height: 50%;
	.board2{
		padding:0px;
	}
		.row1{
			margin-top: 30px;
			width: 100%;
		}
		.row2{
			margin: 0px;
			padding: 0px;
			width: 100%;
		}
		.row3{
			margin: 0px;
			padding: 0px;
			width: 100%;
		}
	}
}