$ScissorsGradient: linear-gradient(45deg, hsl(39, 89%, 49%), hsl(40, 84%, 53%));
$PaperGradient: linear-gradient(45deg,hsl(230, 89%, 62%), hsl(230, 89%, 65%));
$RockGradient: linear-gradient(45deg,hsl(349, 71%, 52%),hsl(349, 70%, 56%));
$LizardGradient: linear-gradient(45deg,hsl(261, 73%, 60%), hsl(261, 72%, 63%));
$Cyan: linear-gradient(45deg,hsl(189, 59%, 53%), hsl(189, 58%, 57%));


$DarkText: hsl(229, 25%, 31%);
$ScoreText: hsl(229, 64%, 46%);
$HeaderOutline: hsl(217, 16%, 45%);


$RadialGradient: radial-gradient(circle at top, hsl(214, 47%, 23%), hsl(237, 49%, 15%) );


@import url('https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,600;0,700;1,600;1,700&display=swap');
$googleFont: 'Barlow Semi Condensed', sans-serif;


*{	
	margin:0px;
}
html, body{
	height: 100%;
	
}

#root{
	height: 100%;
	box-sizing: border-box;
	background: $RadialGradient;
}
.App{
	height: 90vh;
	box-sizing: border-box;
	text-align: center;
	padding: 20px 15%;
	padding-bottom: 0px;
	display: grid;
	grid-template-rows: 1fr 20px;
	font-family: $googleFont;
}
.result-block{
	margin: 0px 15%;
	font-weight: bold;
	color: white;
	font-size: 32px;
	line-height: 28px;
	
	text-transform: uppercase;
	display: flex;
	padding: 15px 20px;
	border: 3px solid rgba($color: #ffffff, $alpha: 0.5);
	border-radius: 15px;
	justify-content: space-between;
	.result-block__title{
		text-align: left;
	}
	.result-block__score{
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		line-height: 1;
		font-size: 15px;
		letter-spacing: 1.5px;
		padding: 15px 40px;
		background-color: #fff;
		border-radius: 10px;
		color: rgba($ScoreText, 0.8);
		.counter{
			
			font-size: 50px;
			color: $HeaderOutline;
		}
	}
}

.rules{
	position: absolute;
	bottom: 50px;
	right: 50px;
	border-radius: 10px;
	line-height: 1.5;
	font-size: 12px;
	font-weight: bold;
	letter-spacing: 1.5px;
	background-color: rgba(#fff, 0);	
	color: #fff;;
	border: 3px solid rgba($color: #ffffff, $alpha: 0.5);
	padding: 10px 30px;
}
.attribution{
	position: absolute;
	bottom: 50px;
	left: 50px;
}

@media (max-width:767px) {
	.App{
		padding: 20px 15px;
		padding-bottom: 0px;
		display: grid;
		grid-template-rows: 1fr 30px;
		font-family: $googleFont;
	}
	.result-block{
		margin: 0px 0px;
		font-weight: bold;
		color: white;
		font-size: 32px;
		line-height: 28px;
	}
	.rules{
		position: absolute;
		width: 150px;
		bottom:6%;
		left:50%;
		transform:translate(-50%);
	}
	.attribution{
		width: 100%;
		position: absolute;
		bottom: 2%;
		left: 50%;
		transform:translate(-50%);
		margin: auto;
	}
}