$PaperGradient: linear-gradient(45deg, hsl(230, 89%, 62%), hsl(230, 89%, 65%));
.ep{
	width: 150px;
	height: 160px;
	border-radius: 50%;
	border: none;
	padding: 3px 10px;
	color: inherit;
	background: rgba($color: #000000, $alpha: 0.3);
	margin: auto;
	margin-top: 25px;
	cursor: pointer;
}