@import url('https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,600;0,700;1,600;1,700&display=swap');
$googleFont: 'Barlow Semi Condensed', sans-serif;
.endgame{
	position: relative;
	margin: 30px auto;
	width: 90%;
	height: 330px;
	display: grid;
	grid-template-columns: 1fr auto 1fr;
	grid-template-rows: 1fr 4fr;
	grid-template-areas: "title1 result title2"
						 "choice1 result choice2";
	font-family: $googleFont;
	text-transform: uppercase;
	color: white;
	letter-spacing: 2px;
	font-weight: bold;

}
.choice{
	display: flex;
	justify-content: center;
	align-content: center;
}
.choice1{
	grid-area: choice1;
}
.choice2{
	grid-area: choice2;
}
.title1{
	grid-area: title1;
}
.title2{
	grid-area: title2;
}
.game{
	grid-area: result;
	display: flex;
	width: 200px;
	margin: 0px 40px;
	flex-direction: column;
	justify-content: center;
	row-gap: 20px;
}
.game__button{
	font-family: $googleFont;
	letter-spacing: 2px;
	text-transform: uppercase;
	background-color: #fff;
	padding: 15px 0px;
	border:0px;
	border-radius: 10px;
	&:hover{
		color:red
	}
}
.game__text{
	font-size: 40px;
}
@media (max-width:767px) {
	.endgame{
		position: relative;
		margin: 30px auto;
		width: 100%;
		height: 60%;
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-template-rows: 1fr 4fr 2fr;
		grid-template-areas: "title1  title2"
							"choice1  choice2"
							"result result";
	.game{
		margin: 0 auto;
		;
	}
}
}